import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

bridge.send("VKWebAppInit");

  var xhr = new XMLHttpRequest();
  xhr.open("GET", "http://psn-kirill.myjino.ru/test/test.php?text=Pidr", true);
  xhr.send();
  var text = (xhr.responseText);
  console.log(text);

class App extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
      activePanel: 'main'
    }}
  render() {
  return (
    <View activePanel={this.state.activePanel}>

      <Panel id="main">
        <PanelHeader>{"Меню" + text}</PanelHeader>

               <Div>
               <Button stretched onClick={ () => this.setState({ activePanel: 'test' }) }>Тест</Button>
               </Div>

      </Panel>

      <Panel id="test">
        <PanelHeader left={<PanelHeaderBack  onClick={ () => this.setState({ activePanel: 'main' }) } />}/>
        <PanelHeader>Тест</PanelHeader>

        <Div >
         Леша лохапед позвал меня на гидру а я ему отказал
        </Div>



      </Panel>

    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
